import { Component, Input, OnInit, Output, EventEmitter, OnChanges } from "@angular/core";
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { DefaultLayoutService } from './layout.service';
import { MenuService } from '../shared/services/menu.service';
import { ExtendedMainMenuGroup, MenuTab, MenuItem, Breadcrumb } from '../shared/models/base.model';
import { ActionService } from '../shared/services/action.service';
import { AggregatorService } from '../shared/services/aggregator.service';

@Component({
  selector: 'default-sidebar',
  templateUrl: './sidebar.component.html',
  styleUrls: ['./sidebar.component.scss']
})

export class DefaultSidebarComponent implements OnInit, OnChanges {
  @Input() public menuTabs: MenuTab[] = [];
  @Input() public navigateTo: (menu: string, router: Router) => void;
  @Input() public set: (role: string) => MenuItem;
  @Input() public menuType: string;
  @Input() public setActive: boolean;
  @Output() public change = new EventEmitter<Breadcrumb[]>();
  public currentUrl: string;
  public menuItems: ExtendedMainMenuGroup[] = [];
  public isCollapsedSideBar: string;
  public collapsedItems: ExtendedMainMenuGroup[] = [];
  public isSupplier = false;
  public tabs: { name: string, menu: string, subName: string }[] = [];
  public carouselTile: any;
  public isPageLoad: boolean = false;
  public isMobile: boolean;
  public clicked: boolean;

  constructor(
    public workspaceLayoutService: DefaultLayoutService,
    protected route: ActivatedRoute,
    protected router: Router,
    protected menuService: MenuService,
    protected actionService: ActionService,
    protected aggregatorService: AggregatorService
  ) {
  }

  ngOnChanges(): void {
    this.actionService.executeAsync(() => {
      this.menuService.init(this.menuTabs, () => {
        this.tabs = this.menuService.getTabs();
        this.currentUrl = this.tabs[0].menu;
        this.menuItems = this.menuService.loadFirstTabItems(this.currentUrl);
        this.actionService.executeAsync(() => {
          this.isCollapsedSideBar = 'no-block';
        });
        this.actionService.executeAsync(() => {
          this.selectMenu();
        });
        this.router.events.subscribe((event) => {
          if (event instanceof NavigationEnd) {
            this.actionService.executeAsync(() => {
              this.selectMenu();
            });
          }
        });
      }, this.set);
    });
  }

  ngOnInit(): void {
    if (!this.navigateTo) {
      this.navigateTo = (menu: string, router: Router) => {
        router.navigate(['/', menu, 'dashboard']);
      }
    }
    this.isMobile = this.actionService.isMobile();
    if (this.isMobile) {
      if (this.workspaceLayoutService.verticalEffect == 'overlay') {
        this.workspaceLayoutService.verticalEffect = 'shrink';
      } else {
        this.workspaceLayoutService.verticalEffect = 'overlay';
      }
    }
  }

  public toggleMenu(): void {
    if (this.isMobile && this.menuType == 'LEFT') {
      if (this.workspaceLayoutService.verticalEffect == 'overlay') {
        this.workspaceLayoutService.verticalEffect = 'shrink';
      } else {
        this.workspaceLayoutService.verticalEffect = 'overlay';
      }
    }
  }

  public toggleOpenedSidebar() {
    this.isCollapsedSideBar = this.isCollapsedSideBar === 'yes-block' ? 'no-block' : 'yes-block';
  }

  public loadMenu(menu: string): void {
    this.menuItems = this.menuService.loadMenuItems(menu);
    this.currentUrl = menu.toLocaleLowerCase();
    this.navigateTo(this.currentUrl, this.router);
  }

  public selectItem(item: any): void {
    this.actionService.changeItem(item, this.menuTabs, (items: Breadcrumb[]) => {
      this.change.emit(items);
    });
  }

  private selectMenu(): void {
    var url = this.router.url;
    var menuItems = document.querySelectorAll('.pcoded-navigatio-lavel');
    if (menuItems && menuItems.length > 0) {
      menuItems.forEach((menu: any) => {
        menu.classList.remove('active');
        var children = document.querySelectorAll(`.${menu.text}`);
        if (children && children.length > 0) {
          children.forEach((child) => {
            child.classList.remove('active');
          });
        }
        if (menu.href && menu.href.indexOf(url) > -1) {
          menu.classList.add('active');
        } else {
          if (children && children.length > 0) {
            children.forEach((child: any) => {
              if (child.href && child.href.indexOf(url) > -1) {
                child.classList.add('active');
                menu.classList.add('active');
              }
            });
          }
        }
      });
    }
  }
}
