import { Component, Input, ViewChild, ElementRef } from "@angular/core";
import { DefaultLayoutService } from './layout.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ActionService } from '../shared/services/action.service';
import { NotificationDetail } from '../shared/models/notification.model';
import { UserViewModel, AuthenticationLoginResponse } from '../auth/auth.model';
import { AggregatorService } from '../shared/services/aggregator.service';
import { AuthenticationService } from '../auth/auth.service';
import { KeyConst } from '../shared/constants/key.const';
import { MenuTab, ExtendedMainMenuGroup, MenuItem } from '../shared/models/base.model';
import { MenuService } from '../shared/services/menu.service';

@Component({
    selector: 'default-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.component.scss']
})

export class DefaultHeaderComponent {
    @Input() public notifications: NotificationDetail[] = [];
    @Input() public logo: string;
    @Input() public title: string;
    @Input() public menuType: string;
    @Input() public set: (role: string) => MenuItem;
    @Input() public menuTabs: MenuTab[] = [];
    @ViewChild('menu', { static: true }) public menu: ElementRef;
    public loading: boolean = false;
    public filter: string;
    public user: UserViewModel;
    public isMobile: boolean;
    public currentUrl: string;
    public menuItems: ExtendedMainMenuGroup[] = [];
    public isCollapsedSideBar: string;
    public isSupplier = false;
    public tabs: { name: string, menu: string, subName: string }[] = [];
    public selectedMenu: ExtendedMainMenuGroup;

    constructor(
        public layoutService: DefaultLayoutService,
        protected router: Router,
        protected route: ActivatedRoute,
        protected actionService: ActionService,
        protected aggregatorService: AggregatorService,
        protected authenticationService: AuthenticationService,
        protected menuService: MenuService,
        private _aggregatorService: AggregatorService
    ) { }

    ngOnInit(): void {
        this.isMobile = this.actionService.isMobile();
        if (this.isMobile) {
            this.toggleMenu();
        }
        if (!this.authenticationService.currentUser) {
            this.toggleMenu();
        }

        this.user = this.authenticationService.currentUser;
        this.registerEvents();
    }

    public toggleMenu(): void {
        if (this.layoutService.verticalEffect == 'overlay') {
            this.layoutService.verticalEffect = 'shrink';
        } else {
            this.layoutService.verticalEffect = 'overlay';
        }
    }

    public logout(): void {
        this.authenticationService.logout(null, true);
    }

    public selectItem(item: ExtendedMainMenuGroup): void {
        if (this.menuItems) {
            this.menuItems.forEach((item) => {
                item.selected = false;
            });
        }
        item.selected = true;
        this.selectedMenu = item;
        if (this.selectedMenu.children && this.selectedMenu.children.length > 0) {
            this.selectedMenu.children.forEach((item) => {
                item.selected = false;
            });
            this.selectedMenu.children[0].selected = true;
        }
    }

    public getParentLink(item: ExtendedMainMenuGroup): string {
        if (!item.children || item.children.length == 0) return 'javascript:;';
        var currentItem = item.children[0];
        return currentItem.mainState ? `/${currentItem.mainState}/${currentItem.state}` : `/${currentItem.state}`;
    }

    public getChildLink(item: any): string {
        return item.mainState ? `/${item.mainState}/${item.state}` : `/${item.state}`;
    }

    public selectSubMenu(child: MenuItem): void {
        if (!this.selectedMenu) return;
        if (this.selectedMenu.children) {
            this.selectedMenu.children.forEach((item) => {
                item.selected = false;
            });
        }
        child.selected = true;
    }

    private registerEvents(): void {
        this.initMenu();
        this._aggregatorService.subscribe(KeyConst.LoggedIn, (response: AuthenticationLoginResponse) => {
            this.authenticationService.currentUser = response.user;
            this.user = this.authenticationService.currentUser;
            if (response.menuTabs) {
                this.menuTabs = response.menuTabs;
            }
            this.actionService.executeAsync(() => {
                this.toggleMenu();
                this.setMenu();
            });
        });
    }

    private initMenu(): void {
        this.menuService.init(this.menuTabs, () => {
            this.tabs = this.menuService.getTabs();
            this.currentUrl = this.tabs[0].menu;
            this.menuItems = this.menuService.loadFirstTabItems(this.currentUrl);
            this.actionService.executeAsync(() => {
                this.isCollapsedSideBar = 'no-block';
            });
            this.setMenu();
        }, this.set);
    }

    private setMenu(): void {
        if (this.menuItems) {
            var url = this.router.url;
            this.menuItems.forEach((item) => {
                if (item.children) {
                    item.children.forEach((kid) => {
                        var state = kid.mainState ? kid.mainState : kid.state;
                        if (url.indexOf(state) > -1) {
                            this.actionService.executeAsync(() => {
                                let items = document.querySelectorAll(".top-item");
                                if (items && items.length > 0) {
                                    for (let i = 0; i < items.length; i++) {
                                        const href = (<any>items[i]).href;
                                        if (href && href.toString().indexOf(state) > 0) {
                                            item.selected = true;
                                            this.selectedMenu = item;
                                            var data = url.split('/');
                                            if (data && data.length > 0 && this.selectedMenu.children) {
                                                var currentChild = this.selectedMenu.children.find(s => s.state == data[data.length - 1]);
                                                if (currentChild) currentChild.selected = true;
                                            }
                                            break;
                                        }
                                    }
                                }
                            });
                        }
                    });
                }
            });
        }
    }
}
